<script>
import { Line } from 'vue-chartjs'
import Data from '@/model/data.vue'
import { EventBus } from '@/bus/event-bus.js';

export default {
  extends: Line,
  data: () => ({
    chartdata: {
      labels: Data.zbs_data.dates,
      datasets: []
    },
    options: {
      elements: {
        line: {
          tension: 0
        }
      },
      responsive: true,
      maintainAspectRatio: false,
      legend: {
        position: 'bottom',
      },
      title: {
        display: false,
        text: 'Análisis Zonas Básicas de Salud'        
      }
    }
  }),
  methods: {
    addZBS: (chartObject, zbs) => {
      console.log("Add ZBS via event bus " + zbs)

      const index = Data.zbs_data.zbs.findIndex((element) => element == zbs)

      const r = Math.round (Math.random () * 255);
      const g = Math.round (Math.random () * 255);
      const b = Math.round (Math.random () * 255);

      chartObject.chartdata.datasets.push({
        id: index,
        label: Data.zbs_data.zbs[index],
        backgroundColor: `rgb(${r}, ${g}, ${b})`,
        borderColor: `rgb(${r}, ${g}, ${b})`,
        data: Data.zbs_data.values[index],
        fill: false
      })

      // Force chart update
      chartObject.$data._chart.update()
    },
    removeZBS: (chartObject, zbs) => {
      console.log("Remove ZBS via event bus " + zbs)

      const index = Data.zbs_data.zbs.findIndex((element) => element == zbs)

      const index_on_array = chartObject.chartdata.datasets.findIndex((element) => element.id == index)

      chartObject.chartdata.datasets.splice(index_on_array, 1)

      // Force chart update
      chartObject.$data._chart.update()
    }
  },
  mounted () {
    EventBus.$on('addZBS', (zbs) => this.addZBS(this, zbs));
    EventBus.$on('removeZBS', (zbs) => this.removeZBS(this, zbs));
    this.renderChart(this.chartdata, this.options)
  }
}
</script>