<template>
  <v-app>
    <v-app-bar dense app>
      <v-app-bar-title>Incidencia Acumulada</v-app-bar-title>
      <v-spacer></v-spacer>
      <v-btn icon>
        <v-icon @click="dialog = true">mdi-playlist-edit</v-icon>
      </v-btn>
    </v-app-bar>

    <v-dialog
      v-model="dialog"
      scrollable
      max-height="600px"
    >
      <v-card>
        <v-card-title>Seleccionar Zonas Básicas de Salud</v-card-title>
        <v-divider></v-divider>
        <v-text-field
          v-model="search"
          label="Buscar"
          class="mx-4"
        ></v-text-field>
        <v-divider></v-divider>
          <v-data-table
            v-model="zbsAutocomplete"
            :headers="headers"
            :items="lastData"
            :items-per-page="5"
            :single-select=false
            :search="search"
            item-key="name"
            show-select
            class="elevation-1"
          ></v-data-table>
          <v-btn
            elevation="2" @click="dialog = false"
          >Cerrar</v-btn>
      </v-card>
    </v-dialog>

    <v-main>
      <v-container fluid>
        <Chart />
      </v-container>
    </v-main>

    <v-footer app>
      Datos obtenidos del Portal de Datos Abiertos de la Comunidad de Madrid. Fecha de los datos: {{ lastDate }}
    </v-footer>
  </v-app>
</template>

<script>
import Chart from '@/components/Chart.vue'
import { EventBus } from '@/bus/event-bus.js';
import Data from '@/model/data.vue'

export default {
  name: 'App',

  components: {
    Chart
  },

  watch:{
    zbsAutocomplete(val, oldval) {
      if (val.length > oldval.length) {
        // Add elements
        let newVal = val.filter(x => !oldval.includes(x));
        newVal.forEach(element => {console.debug("(watcher) Add ZBS " + element.name);this.addZBS(element.name)})
      } else {
        // Remove elements
        let oldVal = oldval.filter(x => !val.includes(x));
        oldVal.forEach(element => {console.debug("(watcher) Remove ZBS " + element.name);this.removeZBS(element.name)})
      }
    }
  },

  data: () => ({
    headers: [
      {
        text: 'Zona Básica de Salud',
        align: 'start',
        sortable: false,
        value: 'name',
      },
      { text: 'Tasa de infección a 14 días', value: 'last_value' }
    ],
    selectedZBSs: [],
    zbsAutocomplete: [],
    dialog: false,
    zbs: Data.zbs_data.zbs,
    lastData: [],
    search: '',
    lastDate: null
  }),
  methods: {
    addZBS(zbs) {
      console.debug("Add ZBS " + zbs)
      if (!this.selectedZBSs.includes(zbs)) {
        this.selectedZBSs.push(zbs)
        EventBus.$emit('addZBS', zbs);
      }
    },
    removeZBS(zbs) {
      console.debug("Remove ZBS " + zbs)
      if (this.selectedZBSs.includes(zbs)) {
        const index = this.selectedZBSs.findIndex((element) => element == zbs)
        this.selectedZBSs.splice(index, 1)
        EventBus.$emit('removeZBS', zbs);
      }
    },
  },
  created: function () {
    // Format data for dialog table
    for(let i=0;i<this.zbs.length;i++) {
      this.lastData.push({"name": Data.zbs_data.zbs[i], "last_value": Data.zbs_data.values[i][Data.zbs_data.values[i].length-1]})
    }

    // Formate date of updated data
    let lastDateUSFormat = Data.zbs_data.dates[Data.zbs_data.dates.length-1].split("/")
    this.lastDate = `${lastDateUSFormat[2]}/${lastDateUSFormat[1]}/${lastDateUSFormat[0]}`
  }
}
</script>